.spanBlock {
  display: flex;
  align-items: center; /* –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –∏ –∏–∫–æ–Ω–∫–∏ –ø–æ —Ü–µ–Ω—Ç—Ä—É */
  gap: 6px; /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –∏–∫–æ–Ω–∫–æ–π –∏ —Ç–µ–∫—Å—Ç–æ–º */
}

.span {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0;
  color: #0080ff;
  width: 100px;
}

.input {
  font-family: 'Montserrat', sans-serif; /* —à—Ä–∏—Ñ—Ç */
  font-weight: 500; /* Medium / 500 */
  font-style: normal; /* Medium –æ–±—ã—á–Ω–æ –±–µ—Ä—ë–º normal */
  font-size: 16px; /* —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ */
  line-height: 36px; /* —á—Ç–æ–±—ã —Å–æ–≤–ø–∞–¥–∞–ª–æ —Å –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–æ–º */
  color: #000000; /* —á—ë—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç */
  width: 276px; /* –∑–∞–Ω–∏–º–∞–µ—Ç –≤—Å—é —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
  height: 36px; /* —á—Ç–æ–±—ã —Å–æ–≤–ø–∞–¥–∞–ª–æ —Å –±–ª–æ–∫–æ–º span */
  padding: 0px; /* –æ—Ç—Å—Ç—É–ø —Å–ª–µ–≤–∞ –∏ —Å–ø—Ä–∞–≤–∞ */
  box-sizing: border-box; /* —á—Ç–æ–±—ã padding –Ω–µ –ª–æ–º–∞–ª —à–∏—Ä–∏–Ω—É */
  border: 1px solid #575656; /* —Ä–∞–º–∫–∞ –¥–ª—è –∏–Ω–ø—É—Ç–∞ */
  border-radius: 4px; /* —Å–∫—Ä—É–≥–ª–µ–Ω–∏–µ —É–≥–ª–æ–≤ */
  box-sizing: border-box; /* —á—Ç–æ–±—ã padding –Ω–µ –ª–æ–º–∞–ª —à–∏—Ä–∏–Ω—É */
}

.pen {
  position: relative;
  width: 24px; /* —à–∏—Ä–∏–Ω–∞ –∏–∫–æ–Ω–∫–∏ */
  height: 24px; /* –≤—ã—Å–æ—Ç–∞ –∏–∫–æ–Ω–∫–∏ */
  cursor: pointer; /* —á—Ç–æ–±—ã –±—ã–ª–æ –ø–æ–Ω—è—Ç–Ω–æ, —á—Ç–æ –º–æ–∂–Ω–æ –∫–ª–∏–∫–Ω—É—Ç—å */
  margin-top: -6px;
  margin-left: 70px;
}
