.message {
  display: flex;
  justify-content: flex-end; /* –≤–µ—Å—å –±–ª–æ–∫ —Å–æ–æ–±—â–µ–Ω–∏—è —Å–¥–≤–∏–Ω—É—Ç –≤–ø—Ä–∞–≤–æ */
  position: relative; /* –¥–ª—è –≤—Ä–µ–º–µ–Ω–∏ –ø–æ–¥ –∞–≤–∞—Ç–∞—Ä–æ–º */
  margin: 10px 0;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse; /* –∞–≤–∞—Ç–∞—Ä —Å–ø—Ä–∞–≤–∞, —Ç–µ–∫—Å—Ç —Å–ª–µ–≤–∞ –æ—Ç –Ω–µ–≥–æ */
  align-items: flex-start; /* –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º –≤–µ—Ä—Ö –∞–≤–∞—Ç–∞—Ä–∞ –∏ —Ç–µ–∫—Å—Ç–∞ */
  gap: 10px;
  position: relative;
}

.imageAndText img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  align-self: flex-end; /* –∞–≤–∞—Ç–∞—Ä —Ç–µ–ø–µ—Ä—å –≤–Ω–∏–∑—É –±–ª–æ–∫–∞ */
}

.text {
  background-color: #0066cc;
  color: white;
  padding: 10px 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-end; /* –∏–º—è –∏ —Ç–µ–∫—Å—Ç —Å–ø—Ä–∞–≤–∞ */
  width: fit-content; /* –±–ª–æ–∫ —Ä–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç—Å—è –ø–æ–¥ —Ç–µ–∫—Å—Ç */
  position: relative;
  gap: 2px; /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –∏–º–µ–Ω–µ–º –∏ —Ç–µ–∫—Å—Ç–æ–º —Å–æ–æ–±—â–µ–Ω–∏—è */
}

.text:after {
  content: '';
  position: absolute;
  bottom: 0;
  right: -8px; /* —Ö–≤–æ—Å—Ç–∏–∫ —Å–ø—Ä–∞–≤–∞ */
  border-width: 22px 1px 2px 16px;
  border-style: solid;
  border-color: transparent transparent transparent #0066cc;
}

.name {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600; /* SemiBold */
  font-size: 18px;
  line-height: 100%;
  text-align: right;
  margin-bottom: 5px;
}

.messageText {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  text-align: right;
  word-wrap: break-word;
}

.time {
  font-size: 12px;
  color: #999;
  position: absolute;
  bottom: -18px; /* –ø–æ–¥ –∞–≤–∞—Ç–∞—Ä–æ–º */
  right: 8px;
}
