.radio {
  width: 20px;
  height: 20px;
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 14px;
  appearance: none; /* —É–±–∏—Ä–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π –∫—Ä—É–≥ */
  -webkit-appearance: none; /* –¥–ª—è Safari */
  border: 2px solid black; /* —Ä–∞–º–∫–∞ –∫—Ä—É–∂–∫–∞ */
  border-radius: 50%; /* –∫—Ä—É–≥ */
  position: relative; /* —á—Ç–æ–±—ã ::before –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–ª—Å—è –≤–Ω—É—Ç—Ä–∏ */
  cursor: pointer;
}

.radio::before {
  content: '';
  position: absolute; /* –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä—É–µ–º –≤–Ω—É—Ç—Ä–∏ input */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* —Ü–µ–Ω—Ç—Ä–∏—Ä—É–µ–º */
  width: 10px; /* —Ä–∞–∑–º–µ—Ä –≤–Ω—É—Ç—Ä–µ–Ω–Ω–µ–≥–æ –∫—Ä—É–∂–∫–∞ (—Ç–æ—á–∫–∏) */
  height: 10px;
  border-radius: 50%;
  background-color: transparent; /* –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –ø—É—Å—Ç–æ–π */
}

.radio:checked::before {
  background-color: black;
}

.label {
  display: flex;
  gap: 10px;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
