.sidebar {
  position: fixed;
  top: 0;
  left: -317px; /* —Å–∫—Ä—ã—Ç –∑–∞ —ç–∫—Ä–∞–Ω–æ–º */
  width: 317px;
  height: 100%;
  background-color: #ffffff;
  border-right: 1px solid #1d21261a;
  transition: left 0.3s ease;
  z-index: 1000;
}

.sidebar.open {
  left: 0;
}

.background {
  position: fixed;
  inset: 0;
  background-color: #1d21261a;
  z-index: 50;
}

.nav {
  display: flex;
  flex-direction: column;

  margin-top: 60px; /* –æ—Ç—Å—Ç—É–ø –æ—Ç –≤–µ—Ä—Ö–∞ —Å–∞–π–¥–±–∞—Ä–∞ */
  margin-left: 70px; /* –æ—Ç—Å—Ç—É–ø –æ—Ç –ª–µ–≤–æ–≥–æ –∫—Ä–∞—è —Å–∞–π–¥–±–∞—Ä–∞ */

  gap: 75px; /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É —Å—Å—ã–ª–∫–∞–º–∏ */
}

.nav a {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 22px;
  line-height: 100%;
  color: #000000; /* –ß—ë—Ä–Ω—ã–π —Ü–≤–µ—Ç */
  text-decoration: none;
}

.nav a:hover {
  color: #0059b2; /* —Ü–≤–µ—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.nav a.active {
  color: #0059b2; /* —Ü–≤–µ—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  text-decoration: underline; /* –ø–æ–¥—á—ë—Ä–∫–∏–≤–∞–Ω–∏–µ —Å–Ω–∏–∑—É */
}

/* –∫–Ω–æ–ø–∫–∞ –∑–∞–∫—Ä—ã—Ç–∏—è */
.close {
  position: absolute;
  top: 16px;
  left: 269px;

  width: 24px;
  height: 24px;

  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

.close img {
  width: 100%;
  height: 100%;
}
