.friendMessage {
  display: flex;
  justify-content: flex-start; /* –≤–µ—Å—å –±–ª–æ–∫ —Å–æ–æ–±—â–µ–Ω–∏—è —Å–¥–≤–∏–Ω—É—Ç –≤–ª–µ–≤–æ */
  position: relative; /* –¥–ª—è –≤—Ä–µ–º–µ–Ω–∏ –ø–æ–¥ –∞–≤–∞—Ç–∞—Ä–æ–º */
  margin: 10px 0;
}

.friendImageAndText {
  display: flex;
  flex-direction: row; /* –∞–≤–∞—Ç–∞—Ä —Å–ª–µ–≤–∞, —Ç–µ–∫—Å—Ç —Å–ø—Ä–∞–≤–∞ */
  align-items: flex-start; /* –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º –≤–µ—Ä—Ö –∞–≤–∞—Ç–∞—Ä–∞ –∏ —Ç–µ–∫—Å—Ç–∞ */
  gap: 10px;
  position: relative;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  margin-top: 15px;
}

.friendText {
  background-color: #0066cc; /* –µ—Å–ª–∏ –Ω—É–∂–µ–Ω –¥—Ä—É–≥–æ–π —Ü–≤–µ—Ç ‚Äî –º–µ–Ω—è–µ–º —Ç—É—Ç */
  color: white;
  padding: 10px 15px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* –∏–º—è –∏ —Ç–µ–∫—Å—Ç —Å–ª–µ–≤–∞ */
  width: fit-content;
  position: relative;
  gap: 2px;
}

/* —Ö–≤–æ—Å—Ç–∏–∫ –°–õ–ï–í–ê ‚Äî –∑–µ—Ä–∫–∞–ª—å–Ω–æ */
.friendText:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: -8px; /* —Ö–≤–æ—Å—Ç–∏–∫ —Å–ª–µ–≤–∞ */
  border-width: 22px 16px 2px 1px;
  border-style: solid;
  border-color: transparent #0066cc transparent transparent;
}

.friendName {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 100%;
  text-align: left;
  margin-bottom: 5px;
}

.friendMessageText {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  text-align: left;
  word-wrap: break-word;
}

.friendTime {
  font-size: 12px;
  color: #999;
  position: absolute;
  bottom: -18px; /* –ø–æ–¥ –∞–≤–∞—Ç–∞—Ä–æ–º */
  left: 8px;
}
