.label {
    display: flex;
    align-items: center;

    cursor: pointer;
}

/*input[type="checkbox"] - no*/

.checkbox {
    appearance: none;
    /*–°–≤–æ–π—Å—Ç–≤–æ appearance –∏–∑–º–µ–Ω—è–µ—Ç –≤–Ω–µ—à–Ω–∏–π –≤–∏–¥ —ç–ª–µ–º–µ–Ω—Ç–∞ –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–∞, –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏ –µ–≥–æ —Ñ—É–Ω–∫—Ü–∏–∏.
       –ï—Å–ª–∏ –∑–∞–¥–∞—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ none, —Ç–æ —á–µ–∫–±–æ–∫—Å –ø—Ä–æ–ø–∞–¥–µ—Ç, –Ω–æ –ø–æ –Ω–µ–º—É, —Ç–µ–º –Ω–µ –º–µ–Ω–µ–µ,
       –º–æ–∂–Ω–æ —â–µ–ª–∫–∞—Ç—å –∏ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞ –±—É–¥–µ—Ç –º–µ–Ω—è—Ç—å—Å—è*/

    width: 18px;
    height: 18px;

    background-repeat: no-repeat;
    background-position: center center;

    /* background-size: 90% 90%; */
    vertical-align: middle;
    margin-right: 8px;
    border-radius: 3px;
    border: 2px solid #06c;
    cursor: pointer;
}

.checkbox:checked {
    appearance: none;

    background-image: url('checked.svg');
    /*background: #99ff99;*/
}

.checkbox:focus {
    border-color: #0059b3;
}

.spanClassName {
    font-family: 'Montserrat', sans-serif;
    color: #06c;
}
