.greetingForm {
  display: flex;
  flex-direction: column; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ —Ä–∞—Å–ø–æ–ª–∞–≥–∞–µ–º —Ç–µ–∫—Å—Ç, –∑–∞—Ç–µ–º input+–∫–Ω–æ–ø–∫–∞ */
  align-items: flex-start; /* –≤—Å–µ –±–ª–æ–∫–∏ –ø—Ä–∏–∂–∞—Ç—ã –∫ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
  padding-left: 70px; /* —Å–º–µ—â–∞–µ–º —Ñ–æ—Ä–º—É –æ—Ç –ª–µ–≤–æ–≥–æ –∫—Ä–∞—è */
}

.inputAndButtonContainer {
  display: flex;
  align-items: center;
  gap: 8px; /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É input –∏ –∫–Ω–æ–ø–∫–æ–π */
  margin-top: 8px; /* –æ—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É –æ—Ç —Ç–µ–∫—Å—Ç–∞ "–õ—é–¥–µ–π –¥–æ–±–∞–≤–∏–ª–∏" */
}

.error {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 14px; /* 100% */
  letter-spacing: 0;
  color: #cc0000; /* –∫—Ä–∞—Å–Ω—ã–π –¥–ª—è –æ—à–∏–±–∫–∏ */
  margin-top: 4px; /* –Ω–µ–±–æ–ª—å—à–æ–π –æ—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É –æ—Ç input */
}

.input {
  width: 372px;
  height: 36px;
  padding: 0 8px; /* —á—Ç–æ–±—ã —Ç–µ–∫—Å—Ç –Ω–µ –ø—Ä–∏–ª–∏–ø–∞–ª –∫ –∫—Ä–∞—é */
  border: 1px solid #ccc; /* –æ–±—ã—á–Ω–∞—è —Ä–∞–º–∫–∞ */
  border-radius: 3px;
  outline: none;
  font-size: 14px;
}

.input:focus {
  border-color: #0066cc; /* –ø–æ–¥—Å–≤–µ—Ç–∫–∞ –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ */
}

.errorInput {
  border-color: red; /* –∫—Ä–∞—Å–Ω–∞—è —Ä–∞–º–∫–∞ –ø—Ä–∏ –æ—à–∏–±–∫–µ */
  width: 372px;
  height: 36px;
}

.button {
  width: 78px;
  height: 36px;
  border-radius: 3px;
  background-color: #0066cc;
  color: #fff;
  opacity: 0.5; /* –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –ø–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω–∞—è */
  border: none;
  cursor: pointer;
  transition: opacity 0.2s;
  margin-top: -4px;
}

.button:focus {
  outline: 2px solid #004999;
}

.button:active {
  background-color: #0055aa;
  opacity: 1; /* —á—Ç–æ–±—ã –±—ã–ª–∞ —è—Ä–∫–∞—è –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ */
}

.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background-color: #0066cc;
}

.text {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 14px; /* 100% */
  letter-spacing: 0;
  color: rgba(0, 0, 0, 0.5); /* #00000080 */
}

.greeting {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500; /* Medium */
  font-style: normal; /* Medium –≤ CSS ‚Äî font-weight 500, font-style normal */
  font-size: 14px;
  line-height: 14px; /* 100% –æ—Ç font-size */
  letter-spacing: 0;
  color: #000000; /* —á—ë—Ä–Ω—ã–π */
  margin-top: 8px; /* —á—Ç–æ–±—ã –Ω–µ–º–Ω–æ–≥–æ –æ—Ç—Å—Ç–æ—è—Ç –æ—Ç –∫–Ω–æ–ø–∫–∏/–∏–Ω–ø—É—Ç–∞ */
}
