.input:focus {
  border-color: #4d90fe;
}

/* –±–∞–∑–æ–≤—ã–π input */
.input {
  width: 372px; /* —à–∏—Ä–∏–Ω–∞ –∏–∑ –º–∞–∫–µ—Ç–∞ */
  height: 36px; /* –≤—ã—Å–æ—Ç–∞ –∏–∑ –º–∞–∫–µ—Ç–∞ */
  padding: 0 10px; /* –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã –¥–ª—è —Ç–µ–∫—Å—Ç–∞ */
  border: 1px solid #ccc; /* –æ–±—ã—á–Ω—ã–π –±–æ—Ä–¥–µ—Ä */
  border-radius: 4px; /* —Å–∫—Ä—É–≥–ª–µ–Ω–∏–µ —É–≥–ª–æ–≤ */
  font-size: 14px;
  outline: none;
  box-sizing: border-box;
  opacity: 1; /* –∫–∞–∫ –≤ –º–∞–∫–µ—Ç–µ */
  transition: border 0.2s;
}

.inputWrapper {
  margin-top: 70px; /* –æ—Ç—Å—Ç—É–ø –æ—Ç –≤–µ—Ä—Ö–∞ –º–∞–∫–µ—Ç–∞ –¥–ª—è –ø–µ—Ä–≤–æ–≥–æ –∏–Ω–ø—É—Ç–∞ */
  margin-left: 70px; /* –æ—Ç—Å—Ç—É–ø —Å–ª–µ–≤–∞ –æ—Ç –º–∞–∫–µ—Ç–∞ */
  display: flex;
  flex-direction: column; /* –∏–Ω–ø—É—Ç + —Ç–µ–∫—Å—Ç –æ—à–∏–±–∫–∏ –≤–Ω–∏–∑ */
}

.superInput {
  border-color: #ccc;
}

.errorInput {
  border-color: #cc1439; /* –∫—Ä–∞—Å–Ω—ã–π –±–æ—Ä–¥–µ—Ä –ø—Ä–∏ –æ—à–∏–±–∫–µ */
}

/* —Ñ–æ–∫—É—Å –¥–ª—è –∏–Ω–ø—É—Ç–∞ —Å –æ—à–∏–±–∫–æ–π */
.errorInput:focus {
  border-color: #cc1439; /* —Å–æ—Ö—Ä–∞–Ω—è–µ–º –∫—Ä–∞—Å–Ω—ã–π –±–æ—Ä–¥–µ—Ä, –µ—Å–ª–∏ –µ—Å—Ç—å –æ—à–∏–±–∫–∞ */
}

.error {
  color: #cc1439; /* –∫—Ä–∞—Å–Ω—ã–π —Ü–≤–µ—Ç –æ—à–∏–±–∫–∏ */
  margin-top: 8px; /* –æ—Ç—Å—Ç—É–ø —Ç–µ–∫—Å—Ç–∞ –æ—à–∏–±–∫–∏ –æ—Ç –∏–Ω–ø—É—Ç–∞ */
  font-size: 12px;
  height: 14px; /* —á—Ç–æ–±—ã –Ω–µ –ø—Ä—ã–≥–∞–ª –∫–æ–Ω—Ç–µ–Ω—Ç –ø—Ä–∏ –ø–æ—è–≤–ª–µ–Ω–∏–∏ –æ—à–∏–±–∫–∏ */
  line-height: 14px;
}

.inputWrapper + .inputWrapper {
  margin-top: 48px; /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –ø–µ—Ä–≤—ã–º –∏ –≤—Ç–æ—Ä—ã–º –∏–Ω–ø—É—Ç–æ–º */
}
