.buttons {
  margin-top: 132px; /* –æ—Ç –∏–Ω–ø—É—Ç–æ–≤ */
  margin-bottom: 55px; /* –æ—Ç –Ω–∏–∂–Ω–µ–π –≥—Ä–∞–Ω–∏—Ü—ã –±–ª–æ–∫–∞ */
  display: flex;
  flex-direction: column; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ */
}

/* –Ω–µ–∞–∫—Ç–∏–≤–Ω–∞—è –∫–Ω–æ–ø–∫–∞ */
.disabled {
  width: 167px;
  height: 30px;
  border-radius: 3px;
  padding: 5px 24px;
  gap: 10px;
  background-color: #004d99;
  color: #fff;
  opacity: 0.5;
  cursor: not-allowed;
  border: none;
}

/* –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∫–Ω–æ–ø–∫–∞ (secondary) */
.secondary {
  width: 176px;
  height: 30px;
  border-radius: 3px;
  padding: 5px 24px;
  gap: 10px;
  background-color: transparent;
  color: #0066cc;
  border: 1px solid #0066cc;
  opacity: 1;
}

.default {
  width: 167px;
  height: 30px;
  border-radius: 3px;
  padding: 5px 24px;
  gap: 10px;
  background-color: #0066cc;
  color: #fff;
  opacity: 1;
  border: none;
}

/* –æ–ø–∞—Å–Ω–∞—è –∫–Ω–æ–ø–∫–∞ (red) */
.red {
  width: 167px;
  height: 30px;
  border-radius: 3px;
  padding: 5px 24px;
  gap: 10px;
  background-color: #cc1439;
  color: #fff;
  opacity: 1;
  border: none;
}

.default:hover {
  background-color: #005bb5; /* —á—É—Ç—å —Ç–µ–º–Ω–µ–µ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.red:hover {
  background-color: #b01233;
}

.secondary:hover {
  background-color: #e6f0ff; /* –ª—ë–≥–∫–∏–π —ç—Ñ—Ñ–µ–∫—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.default:active {
  background-color: #004c99; /* –µ—â—ë —Ç–µ–º–Ω–µ–µ –ø—Ä–∏ –∫–ª–∏–∫–µ */
}

.secondary:active {
  background-color: #cce0ff; /* –∞–∫—Ç–∏–≤–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ */
}

.red:active {
  background-color: #990f2a;
}

/* —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–π disabled –¥–ª—è –≤—Å–µ—Ö –∫–Ω–æ–ø–æ–∫ */
.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.button-wrapper {
  position: relative;
  display: inline-block;
}

.default:hover::after,
.red:hover::after,
.secondary:hover::after {
  content: '–ü—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏';
  position: absolute;
  top: 100%; /* —á—É—Ç—å –Ω–∏–∂–µ –∫–Ω–æ–ø–∫–∏ */
  left: 0;
  font-size: 12px;
  color: #000;
}

.default:active::after,
.red:active::after,
.secondary:active::after {
  content: '–ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏';
  position: absolute;
  top: 100%; /* —á—É—Ç—å –Ω–∏–∂–µ –∫–Ω–æ–ø–∫–∏ */
  left: 0;
  font-size: 12px;
  color: #000;
}

.buttonWrapper {
  position: relative;
  display: inline-block;
}

.tooltip {
  position: absolute;
  top: 100%; /* —á—É—Ç—å –Ω–∏–∂–µ –∫–Ω–æ–ø–∫–∏ */
  left: 0;
  font-size: 12px;
  color: #000;
  background: #fff;
  padding: 2px 4px;
  border-radius: 2px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
  white-space: nowrap;
  margin-top: 4px;
  z-index: 10;
}
